body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
  background-color: white;
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  grid-template-rows: auto;
  border-bottom: 1px solid black;
  padding: 1vh;
  align-content: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 2;
  height: 100px;
}

.burger-menu-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.burger-menu {
  width: 37px;
  height: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.burger-line {
  width: 35px;
  height: 4px;
  border-radius: 50px;
  background-color: #000;
  transition: transform 0.3s ease;
}

.open .burger-line:first-child {
  transform: translateY(10px) rotate(45deg);
}

.open .burger-line:nth-child(2) {
  opacity: 0;
  align-self: center;
  justify-self: center;
}

.open .burger-line:last-child {
  transform: translateY(-11px) rotate(-45deg);
  align-self: center;
  justify-self: center;
}

.burger-menu-background {
  font-size: larger;
  position: relative;
  top: 0%;
  right: 0%;
  background-color: white;
  border: 2px solid black;
  border-radius: 10px;
  display: block;
  flex-direction: row;
  align-items: center;
  z-index: 3;
}

.menu-options {
  font-family: "Nabla", cursive;
  font-family: "Poppins", sans-serif;
  border-radius: 9px;
  color: black;
  text-decoration: none;
  padding-left: 5px;
  padding-right: 5px; /* Adds some right margin for spacing */
}

.menu-options:hover{
  background-color: #21212155;;
}

.home{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 90vh;
  background-color: rgb(238, 238, 238);
}

.home h6{
  font-family: "Nabla", cursive;
  font-family: "Poppins", sans-serif;
  font-size: larger;
  font-weight: 100;
}

.home p{
  font-family: "Nabla", cursive;
  font-family: "Poppins", sans-serif;
}

.home_page_logo{
  height: 100px;
  width: 400px;
  padding: 2px;
}

.buttons_container{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.language-button-homepage {
  display: block;
  text-decoration: none;
  background-color: rgb(225, 225, 225);
  margin: 1rem;
  border-radius: 100rem;
  padding: 1.5rem;
  border: 0px;
  position: relative;
}

.language-button-homepage p {
  font-size: 2vh;
  color: black;
}

.language-button-homepage:hover {
  background-color: rgb(214, 214, 214);
  cursor: pointer;
  z-index: 1;
  transform: scale(1.05);
  transition: transform 0.3s ease; /* Optional: Add a smooth transition effect */
}


.language-button-homepage:active{
  background-color: antiquewhite;
}

.button-image{
  height: 20vh;
}

.clicked .button-image {
  animation: spin-scale 1.5s linear;
  transform-origin: center;
}

@keyframes spin-scale {
  0% {
    transform: rotate(0deg) scale(1);
  }
  100% {
    transform: rotate(180deg) scale(12);
  }
}

.language-main{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background-color: rgb(238, 238, 238);
}

.words-list {
  background-color: rgb(238, 238, 238);
  list-style: none;
  padding: 0;
  font-family: "Nabla", cursive;
  font-family: "Poppins", sans-serif;
}

.word {
  border-bottom: black 0.4rem solid;
  border-radius: 8rem;
  background-color: rgb(250, 250, 250);
  padding: 1rem;
  margin-top: 15rem;
  margin-bottom: 30rem;
  margin-left: 20rem;
  margin-right: 20rem;
}

.correctWord{
  border-bottom: rgb(23, 98, 23) 0.4rem solid;
  border-radius: 8rem;
  background-color: rgb(250, 250, 250);
  margin-top: 15rem;
  margin-bottom: 30rem;
  margin-left: 20rem;
  margin-right: 20rem;
  transition: border-top .5s;
}

.words-list-item {
  list-style: none;
  background-color: rgb(250, 250, 250);
  border: rgb(250, 250, 250) solid 3px;
  border-radius: 4rem;
  margin: 2rem;
  padding: 1rem;
  font-size: large;
  font-weight: 300;
  font-family: "Nabla", cursive;
  font-family: "Poppins", sans-serif;
}

.words-list-item:hover{
  background-color: rgb(235, 235, 235);
}

.words-list-title{
  border: rgba(63, 184, 63, 0) solid 3px;
  font-size: larger;
  font-weight: bold;
  font-family: "Nabla", cursive;
  font-family: "Poppins", sans-serif;
}

.correct-answer {
  border: rgb(23, 98, 23) solid 3px;
  font-weight: bold;
}

.incorrect-answer {
  border: red solid 3px;
}

.new_round_button{
  font-size: medium;
  margin: 10px;
  border-radius: 20px;
  border: 0;
  padding: 10px;
  background-color: #212121;
  color:rgb(214, 214, 214);
}

.new_round_button:hover{
  cursor: pointer;
  color: #212121;
  background-color: rgb(214, 214, 214);
}

.home {
  list-style: none;
}

.footer-container{
  display: grid;
  align-items: center;
  grid-template-columns: 25% 25% 25% 25%;
