  animation: bubble_animation 2s ease; /* Added animation properties */
}

@keyframes bubble_animation {
  0% {
    transform: rotate(-180deg) scale(0); /* Start position */
  }
  80% {
    transform: rotate(-360deg) scale(1.1)
  }
  100% {
    transform: rotate(-360deg) scale(1); /* End position */
